import React, { useEffect, useState } from 'react';
import { Wrapper } from "./Styles";
import { Row, Col, message, List } from "antd"
import { translate } from 'react-polyglot';
import qs from "querystring";
import { useSelector } from 'react-redux';
import { getService } from '../../services';
import CardpackItem from '../../components/card/CardpackItem';
import Loader from '../../components/loader/';


const ViewAll = ({ filterDefaultValues = {}, ...props }) => {
    /* console.log(props); */
    const [dataSource, setDataSource] = useState([]);
    const [loading, setLoading] = useState(true);

    const entity = useSelector(({ auth }) => {
        return auth.entity || [];
    });
    const getData = (params = {}) => {
        const service = getService("packs");
        service.find({
            query: {
                ...params,
                ...filterDefaultValues,
                entity_id: entity.id
            }
        })
            .then(({ data }) => {
                setLoading(false);
                setDataSource(data);
            })
            .catch(err => {
                setLoading(false);
                message.error(err.message);
            });
    }

    useEffect(() => {
        if (props.location) {
            let { search } = props.location;
            search = qs.parse(search.replace(/\?/g, ''));
            getData(search);
        }
    }, [props.location])
    if (loading) return <Loader />
    return (

        <Wrapper>
            <Row className="section-container-container" justify="center" type="flex" align="middle" gutter="10">
                <Col span={22}>
                    <List

                        dataSource={dataSource}
                        loading={loading}
                        grid={props.grid || {
                            gutter: 16,
                            xs: 1,
                            sm: 2,
                            md: 3,
                            lg: 3,
                            xl: 4,

                        }}
                        renderItem={item => (
                            <List.Item>
                                <CardpackItem
                                    reference="packs"
                                    record={item}
                                />
                            </List.Item>
                        )}
                    />,
            </Col>
            </Row>
        </Wrapper>
    )
}
export default translate()(ViewAll);